/* Custom dropdown */
.mainFunc .custom-dropdown {
  position: relative;
  top: 0px;
  display: inline-block;
  vertical-align: middle;
  margin: 10px;
}

.mainFunc .custom-dropdown select {

  background-color: #1abc9c;
  width: 300px;
  color: #fff;
  font-size: inherit;
  padding: .5em;
  padding-right: 2.5em; 
  border: 0;
  margin: 0;
  border-radius: 3px;
  text-indent: 0.01px;
  text-overflow: '';
  -webkit-appearance: button; /* hide default arrow in chrome OSX */
}

.mainFunc .custom-dropdown::before,
.custom-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none;
}

.mainFunc .custom-dropdown::after { /*  Custom dropdown arrow */
  content: "\25BC";
  height: 1em;
  font-size: .625em;
  line-height: 1;
  right: 1.2em;
  top: 50%;
  margin-top: -.5em;
}

.mainFunc .custom-dropdown::before { /*  Custom dropdown arrow cover */
  width: 2em;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0 3px 3px 0;
}

.mainFunc .custom-dropdown select[disabled] {
  color: rgba(0,0,0,.3);
}

.mainFunc .custom-dropdown select[disabled]::after {
  color: rgba(0,0,0,.1);
}

.mainFunc .custom-dropdown::before {
  background-color: rgba(0,0,0,.15);
}

.mainFunc .custom-dropdown::after {
  color: rgba(0,0,0,.4);
}

